<template>
  <v-layout>
    <v-flex>
      <v-jumbotron color="primary" dark>
        <v-container fill-height>
          <v-layout align-center>
            <v-flex text-xs-center>
              <h3 class="display-3"> LIST </h3>
            </v-flex>
          </v-layout>
        </v-container>
      </v-jumbotron>
      <v-list>
        <div v-for="(item, index) in $store.state.nabi.items" :key="index">
          <v-list-tile :to="'/' + item.nabi_id">
            <v-list-tile-content>
              <v-list-tile-title>
                {{ item.nama }}
              </v-list-tile-title>
            </v-list-tile-content>
          </v-list-tile>
          <v-divider></v-divider>
        </div>
      </v-list>
    </v-flex>
  </v-layout>
</template>

<script>
export default {
  mounted () {
    this.$store.commit('kisahNabiInterface/showDrawer', false)
    this.$idb.nabi.getItem('nabi').then(val => {
      this.$store.commit('nabi/items', val)
    })
    this.$axios.get(this.$apiUrl + 'nabi').then(resp => {
      this.$idb.nabi.setItem('nabi', resp.data.data).then(val => {
        this.$store.commit('nabi/items', val)
      })
    })
  }
}
</script>




