import Vue from 'vue'
import Router from 'vue-router'
import Nabi from '@/components/nabi/Index'
import NabiView from '@/components/nabi/View'
import Kontak from '@/components/kontak/Index'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Nabi',
      component: Nabi
    },
    {
      path: '/kontak',
      name: 'Kontak',
      component: Kontak
    },
    {
      path: '/:id',
      name: 'NabiView',
      component: NabiView
    }
  ]
})



