export default {
  namespaced: true,
  state: {
    toggleDrawer: false,
    showDrawer: false
  },
  mutations: {
    toggleDrawer: (state) => {
      state.toggleDrawer = !state.toggleDrawer
    },
    showDrawer: (state, data) => {
      state.showDrawer = data
    }
  },
  getters: {
    toggleDrawer: (state) => {
      return state.toggleDrawer
    }
  }
}



