<template>
  <v-navigation-drawer
    temporary
    v-model="$store.state.kisahNabiInterface.toggleDrawer"
    fixed
    right=""
  >
    <v-list class="pa-0">
      <v-tabs>
        <v-btn icon style="color:red;" @click="$store.commit('kisahNabiInterface/toggleDrawer')">
          <v-icon>close</v-icon>
      </v-btn>
        <v-tab>
          Kisah
        </v-tab>
      </v-tabs>
    </v-list>
    <v-list class="pt-0" dense>
      <v-divider></v-divider>
      <div v-for="(item, index) in $store.state.kisah.items.kisah" :key="index">
        <v-list-tile @click.prevent="goTo(item.judul.split(' ').join('_'))">
          <v-list-tile-action>
            {{ index + 1 }}
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>
              {{ item.judul }}
            </v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
        <v-divider></v-divider>
      </div>
    </v-list>
  </v-navigation-drawer>
</template>

<script>
export default {
  methods: {
    goTo (selector) {
      this.$vuetify.goTo('#' + selector, {
        duration: 500,
        offset: -50
      })
      this.$store.commit('kisahNabiInterface/toggleDrawer')
    }
  }
}
</script>




