<template>
  <v-container>
    <v-layout>
      <v-flex>
        <v-card v-for="(item, index) in $store.state.kontak.items" :key="index" 
        style="margin-bottom:25px;">
          <v-card-text class="text-xs-center">
            <div>
              <div class="body-2">Nama</div>
              <div style="margin:10px 0;"> {{ item.nama }} </div>
            </div>
            <div>
              <div class="body-2">Email</div>
              <div style="margin:10px 0;"> {{ item.email }} </div>
            </div>
            <div v-if="item.twitter">
              <div class="body-2">Twitter</div>
              <div style="margin:10px 0;"> {{ item.twitter }} </div>
            </div>
          </v-card-text>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  mounted () {
    this.$idb.kontak.getItem('kontak').then(val => {
      this.$store.commit('kontak/items', val)
    })
    this.$axios.get(this.$apiUrl + 'kontak').then(resp => {
      this.$idb.kontak.setItem('kontak', resp.data.data).then(val => {
        this.$store.commit('kontak/items', val)
      })
    })
  }
}
</script>




