import Vue from 'vue'
import Vuex from 'vuex'
import kisah from '@/store/kisah.js'
import nabi from '@/store/nabi.js'
import kontak from '@/store/kontak.js'
import kisahNabiInterface from '@/store/kisahNabiInterface.js'

Vue.use(Vuex)

export default new Vuex.Store({
  modules: {
    kisah, nabi, kontak, kisahNabiInterface
  }
})



