<template>
  <v-layout>
    <v-flex>
        <v-jumbotron color="primary" dark>
          <v-container fill-height>
            <v-layout align-center>
              <v-flex text-xs-center>
                <h3 class="display-3"> {{ this.$store.state.kisah.items.nama }} </h3>
              </v-flex>
            </v-layout>
          </v-container>
        </v-jumbotron>
        <div v-for="(item, index) in $store.state.kisah.items.kisah" :key="index">
          <v-container :id="item.judul.split(' ').join('_')">
            <h2>{{ item.judul }}</h2>
            <p>
              {{ item.konten }}
            </p>
          </v-container>
        </div>
    </v-flex>
  </v-layout>
</template>

<script>
export default {
  mounted () {
    this.$store.commit('kisahNabiInterface/showDrawer', true)
    this.$idb.kisah.getItem(this.$route.params.id).then(val => {
      this.$store.commit('kisah/items', val)
    })
    this.$axios.get(this.$apiUrl + 'nabi/' + this.$route.params.id).then(resp => {
      this.$idb.kisah.setItem(resp.data.data.nabi_id, resp.data.data).then(val => {
        this.$store.commit('kisah/items', val)
      })
    })
  }
}
</script>

<style>
.image-title {
  display:block;
  padding:20px;
  background-color:royalblue;
  font-size:33px;
  color:white;
  text-align:center;
}
</style>




