<template>
  <v-app>
    <v-toolbar
      app
      color="primary"
      dark
    >
      <v-btn icon to="/">
        <v-icon>home</v-icon>
      </v-btn>
      <v-toolbar-title v-text="title"></v-toolbar-title>
      <v-spacer></v-spacer>
      <v-btn icon to="/kontak">
        <v-icon>email</v-icon>
      </v-btn>
      <v-toolbar-side-icon
      v-if="$store.state.kisahNabiInterface.showDrawer" 
      @click="$store.commit('kisahNabiInterface/toggleDrawer')"></v-toolbar-side-icon>
    </v-toolbar>
    <v-content>
      <router-view/>
    </v-content>
    <drawer-kisah></drawer-kisah>
    <div>

    </div>
  </v-app>
</template>

<script>
import DrawerKisah from '@/components/nabi/DrawerKisah'
export default {
  components: {
    DrawerKisah
  },
  data () {
    return {
      title: 'Kisah nabi'
    }
  },
  name: 'App'
}
</script>



